// ë‹¤ë¥¸ ë°ì´í„°ë“¤ë„ ì¶”ê°€ë ìˆ˜ ìˆê¸° ë•Œë¬¸ì—
const store = {
  currentPage : 1, // í˜„ì¬ í˜ì´ì§€ ì´ˆê¸°ê°’ 1
  maxPage : null,
};


// ì…ë ¥ì€ ì¸ìë¡œ ë°›ì„ ìˆ˜ ìˆìŒ
